const activeTabIndex = ref(0)
const role = sessionStorage.getItem("role");
const router = useRouter()
//
const id = ref('');
function handleTabChange(e) {
    const str = sessionStorage.getItem('role')
    console.log(str);
    const index = e.index;
    console.log(str);
    console.log('1111111');
    if (index === 0) {
        
    } else if (index === 1) {
        router.push('/message');
    } else if (index === 2) {
        if (str === 'patient') router.push('/patientRoot');
        else router.push('/doctorRoot');
    }
}
onMounted( ()=>{
    getinfo();
    console.log(sessionStorage.getItem("role"));
    console.log(sessionStorage.getItem("token"));
})

//token
const token = sessionStorage.getItem('token');
// 
function getinfo() {
    axios.get('http://121.199.161.134:8080/getDoctorInformation', {
        params: {
            // token: token.value
            token:token
        }
    })
        .then(response => {
            // 
            console.log(response.data)
            const data = response.data.data;
            namecontent.value = data.doctorName;
            deptcontent.value = data.doctorSection;
            titlecontent.value = data.doctorTitle;
            emailcontent.value = data.doctorMail;
            idCardcontent.value = data.doctorNumber;
            phonecontent.value = data.doctorPhone;
            historycontent.value = data.doctorIntroduction;
        })
        .catch(error => {
            // 
            console.error(error)
        })
}

// 
const pid = ref('');

//
// 
const nameediting = ref(false)
const namecontent = ref('')
const nametempContent = ref('')
function namestartEditing() {
    nameediting.value = true
    nametempContent.value = namecontent.value
    judmess.value = 0
}
function namestopEditing() {
    nameediting.value = false
