}

//
const historyediting = ref(false)
const historycontent = ref('d')
const historytempContent = ref('')
const bthis = ref('')
const hisobject = {
}
//
const judhis = ref(0);
function edithistory() {
    if (bthis.value == '') {
        historyediting.value = true;
        historytempContent.value = historycontent.value;
        bthis.value = '';
    }
    else {
        hisobject.doctorIntroduction = historytempContent.value;
        //
        axios.post("http://121.199.161.134:8080/saveIntroduction",hisobject, {
            params:{
                token: token,
            }
            }).then(res => {
            console.log(res);
            judhis.value = 1;
        }).catch(error => {
            console.error(error)
            judhis.value = -1;
        })

        historyediting.value = false;
        historycontent.value = historytempContent.value;
        bthis.value = '';
    }

}

//
const doctor = {};
function setDoctor() {
    doctor.doctorMail = emailcontent.value;
    doctor.doctorPhone = phonecontent.value;
    doctor.doctorName = namecontent.value;
    doctor.doctorNumber = idCardcontent.value;
    doctor.doctorSection = deptcontent.value;
    doctor.doctorTitle = titlecontent.value;
}
//,
const judmess = ref(0);
function save() {
    setDoctor();
    axios.post('http://121.199.161.134:8080/changeDoctor',doctor,{
        params:{
            token: token
        }
})
        .then(response => {
            console.log(response.data)
            judmess.value = 1;
        })
        .catch(error => {
            console.error(error)
            judmess.value = -1;
        })
}

//
const imageUrl = ref('');
//formdata
const formData = new FormData();
//
const judpic = ref(0);
function changePic(event) {
    const file = event.target.files[0];
    if (file) {
        const reader = new FileReader();

        reader.onload = () => {
            imageUrl.value = reader.result;
        };
        reader.readAsDataURL(file);
    }
    //
    if (file?.file) {
        const blob = new Blob([file], { type: file.type });
        formData.append('file', blob, { filename: 'image.jpg' });
        axios.post('http://121.199.161.134:8080/savePic', formData, {
            params: {
                pid: pid.value,
                image: formData.values,
            }
        }).then(response => {
            console.log('Upload successful!');
            judpic.value = 1;
        }).catch(error => {
            console.error('Upload failed: ', error);
            judpic.value = -1;
        });
    }
    else {
        console.error('')
    }
}
//
function showList(){
    var list = document.getElementById("header_list");
    console.log("in");
    list.style.display = "block";
};
function unShowList(){
    var list = document.getElementById("header_list");
    console.log("out");
    list.style.display = "none";
};
//
function outlogin(){
    sessionStorage.removeItem('role');
    sessionStorage.removeItem('token');
    window.location.href = '/login';
};
function zhuxiao(){
    if(sessionStorage.getItem('role') == 'patient'){
        axios.post('http://121.199.161.134:8080/zhuxiaoDoctor',null,{
        params:{
            token: token
        }
    })
        .then(response => {
            console.log(response.data)
            outlogin();
        })
        .catch(error => {
            console.error(error)
        })
    }
    else{
        axios.post('http://121.199.161.134:8080/zhuxiaoPatient',null,{
        params:{
            token: token
        }
    })
        .then(response => {
            console.log(response.data)
            outlogin();
        })
        .catch(error => {
            console.error(error)
        })
    }
function jumpmessage(){
    window.location.href = '/message';
}
}
