<script setup>
import { ref } from 'vue';
import axios from 'axios'
//primevue
import InputText from 'primevue/inputtext';
import Button from 'primevue/button';
import Password from 'primevue/password';
import SelectButton from 'primevue/selectbutton';
import Message from 'primevue/message';

const uid = ref('P00000000000');
const password = ref('newpassword');
const options = ref(['', '']);
const is_patient = ref('off');
const role = ref('patient')//
//
const juglog = ref(0);
function loginbt() {
    //
    if (is_patient.value === '') role.value = 'patient';
    else role.value = 'doctor';
    console.log(role.value);
    if (role.value == 'patient') {
        axios.post('http://121.199.161.134:8080/loginPatient',null, {
            params: {
                uid: uid.value,
                password: password.value,
            }}
        )
            .then(response => {
                console.log(response.data.code);
                //
                const jud =response.data.code;
                console.log(response.data.data);
                if(jud == 1){
                    console.log(response.data);
                    sessionStorage.setItem('token', response.data.data);
                    sessionStorage.setItem('role', role.value);//yn
                    juglog.value = 1;
                    window.location.href="/patientRoot";
                }
                //tokensession
                else if(jud == 2){

                    sessionStorage.setItem('token', response.data.data);
                    sessionStorage.setItem('role', role.value);//yn
                    juglog.value = 1;
                    window.location.href="/adminRoot";
                }
                else{
                    console.log('');
                    juglog.value = -1;
                }
            })
            .catch(error => {
                console.error(error)
                judlog.value = -1;
            })
    }
    if (role.value == 'doctor') {
        axios.post('http://121.199.161.134:8080/loginDoctor',null,{params: {
                uid: uid.value,
                password: password.value,
            }}
        )
            .then(response => {
                console.log(response.data.code);
                //
                const jud =response.data.code;
                console.log(typeof(jud));
                if(jud == 1){
                    sessionStorage.setItem('token', response.data.data);
                    sessionStorage.setItem('role', role.value);//yn
                    juglog.value = 1;
                    window.location.href="/doctorRoot";
                }
                //tokensession
                else if(jud == 2){
                    sessionStorage.setItem('token', response.data.data);
                    sessionStorage.setItem('role', role.value);//yn
                    juglog.value = 1;
                    console.log(sessionStorage.getItem('token'));
                    window.location.href="/defaultView";
                }
                else{
                    console.log('');
                    juglog.value = -1;
                }
            })
            .catch(error => {
                console.error(error)
                judlog.value = -1;
            })
    }

}

